import {FormControl, FormGroup, FormLabel} from "react-bootstrap";
import {IDateInput} from "./IDateInput";
import {useFormContext} from "react-hook-form";
import {useEffect} from "react";

export const DateInput = (props: IDateInput) => {
    const {register, setValue, formState:{errors}} = useFormContext();
    const isRequired = props.isRequired || false;

    useEffect(() => {
        setValue(props.name, props.defaultValue);
    }, [props.defaultValue]);

    return <FormGroup className="mb-2">
        {props.label &&
            <FormLabel>{props.label}</FormLabel>
        }
        <FormControl {...register(props.name, {
            required: {value: isRequired, message: "   ."}
        })} type="date"/>
    </FormGroup>
}