import {observer} from "mobx-react-lite";
import {state} from "./modalStore";
import {Modal} from "react-bootstrap";

export const ModalProvider = observer(() => {

    return <>
        {
            state.modal.map((modal, index) => {
                return <Modal key={index} show={true} size={modal.size}>
                    {modal.modal}
                </Modal>
            })
        }
    </>
})
