import {observer} from "mobx-react-lite";
import {Accordion, AccordionHeader, AccordionItem, Button} from "react-bootstrap";
import {deleteLog, getLogsAsync, state} from "../store";
import AccordionBody from "react-bootstrap/AccordionBody";
import {useEffect} from "react";

export const List = observer(() => {

    useEffect(() => {
        getLogsAsync()
    }, [])

    return <Accordion>
        {
            state.state?.logs?.map(log => {
                return <AccordionItem key={log.id} eventKey={log.id.toString()}>
                    <AccordionHeader>
                        <div className="d-flex w-100 justify-content-between me-3">
                            <div className="my-auto">
                                {log.level}  {new Date(log.timeStamp).toLocaleString()}
                            </div>
                            <div>
                                <Button size="sm" variant="danger" onClick={(event) => {
                                    event.stopPropagation();
                                    deleteLog(log.id)
                                }}></Button>
                            </div>
                        </div>
                    </AccordionHeader>
                    <AccordionBody>
                        <div>
                            <div>
                                {log.message}
                            </div>
                            <hr/>
                            <div>
                                {log.stackTrace}
                            </div>
                        </div>
                    </AccordionBody>
                </AccordionItem>
            })
        }
    </Accordion>
})
