import {Button, Form, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {FormProvider, useForm} from "react-hook-form";
import {TextInput} from "../../../../../common/component/input/text/TextInput";
import {NumberInput} from "../../../../../common/component/input/number/NumberInput";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";
import {DateInput} from "../../../../../common/component/input/date/DateInput";
import {addPromoCodeAsync, getPromoCode, resetPromoCode, state, updatePromoCodeAsync} from "./store";
import {useEffect} from "react";
import {yyyy_mm_dd} from "../../../../../common/utils/dateFunctions";
import {observer} from "mobx-react-lite";

export const AddEditPromoCodeModal = observer((props: {id: number}) => {
    const hookForm = useForm()

    useEffect(() => {
        if(props.id > 0)
            getPromoCode(props.id)

        return () => resetPromoCode()
    }, [])

    return <>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit((form) => {
                if(props.id > 0)
                    updatePromoCodeAsync(form)
                else
                    addPromoCodeAsync(form)
            })}>
                <ModalHeader>
                    <ModalTitle>
                        {props.id === 0 ? " " : " "}
                    </ModalTitle>
                </ModalHeader>
                <ModalBody>
                    <TextInput name="id"
                               attributes={{className: "d-none"}}
                               defaultValue={state.code?.id.toString()}/>
                    <TextInput label=":"
                               name="code"
                               isRequired={true}
                               defaultValue={state.code?.code}/>
                    <NumberInput label=""
                                 name="discount"
                                 isRequired={true}
                                 minValue={0}
                                 maxValue={100}
                                 defaultValue={state.code?.discount.toString()}/>
                    <NumberInput label="- "
                                 name="usageCount"
                                 isRequired={true}
                                 minValue={0}
                                 defaultValue={state.code?.usageCount.toString()}/>
                    <DateInput label=" :"
                               name="startDate"
                               isRequired={true}
                               defaultValue={yyyy_mm_dd(new Date(state.code?.startDate))}/>
                    <DateInput label=" :"
                               name="endDate"
                               isRequired={true}
                               defaultValue={yyyy_mm_dd(new Date(state.code?.endDate))}/>
                </ModalBody>
                <ModalFooter>
                    <Button variant="dark" type="submit"></Button>
                    <Button variant="danger" onClick={closeLastModal}></Button>
                </ModalFooter>
            </Form>
        </FormProvider>

    </>
})