import {observer} from "mobx-react-lite";
import {deleteUserAsync, state} from "./store";
import {Button} from "react-bootstrap";
import {PencilFill, TrashFill} from "react-bootstrap-icons";
import {openModal} from "../../../../../common/component/modal/modalStore";
import {AddEdit} from "../add-edit/AddEdit";

export const List = observer(() => {
    return <div>
        {
            state.users?.map(user => {
                return <div className="d-flex text-center py-2">
                    <div className="w-100">
                        {user.name}
                    </div>
                    <div className="w-100">
                        {new Date(user.registerDate).toLocaleDateString()}
                    </div>
                    <div className="w-100">
                        {new Date(user.accessEndDate).toLocaleDateString()}
                    </div>
                    <div className="w-100">
                        {user.lastConnection != undefined ? `${new Date(user.lastConnection).toLocaleDateString()} ${new Date(user.lastConnection).toLocaleTimeString()}` : " "}
                    </div>
                    <div className="d-flex justify-content-center w-100">
                        <Button variant="dark"
                                className="me-2"
                                onClick={() => openModal({modal: <AddEdit id={user.id}/>, size: undefined})}>
                            <PencilFill/>
                        </Button>
                        <Button variant="danger" onClick={() => deleteUserAsync(user.id)}>
                            <TrashFill/>
                        </Button>
                    </div>
                </div>
            })
        }

    </div>
})