import {Button, Form, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";
import {FormProvider, useForm} from "react-hook-form";
import {TextInput} from "../../../../../common/component/input/text/TextInput";
import {SelectInput} from "../../../../../common/component/input/select/SelectInput";
import {useEffect} from "react";
import {addServerAsync, getAddEditServerAsync, state, updateServerAsync} from "./store";
import {observer} from "mobx-react-lite";

export const AddEditServer = observer((props: {id: number}) => {
    const hookForm = useForm();

    useEffect(() => {
        getAddEditServerAsync(props.id)
    }, [props.id]);

    return <>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit((form) => props.id > 0 ? updateServerAsync(form) : addServerAsync(form))}>
                <ModalHeader>
                    <ModalTitle> </ModalTitle>
                </ModalHeader>
                <ModalBody>

                    {props.id > 0 &&
                        <TextInput name="id"
                                   defaultValue={props.id.toString()}
                                   attributes={{className: "d-none"}}/>
                    }

                    <TextInput name="name"
                               label=" :"
                               isRequired={true}
                               defaultValue={state.form?.name}/>
                    <SelectInput name="locationId"
                                 label=":"
                                 isRequired={true}
                                 placeholder=" "
                                 options={state.form?.countries}
                                 defaultValue={state.form?.countries.find(x => x.value == state.form?.countryId.toString())}/>
                    <TextInput name="ip"
                               label="IP :"
                               isRequired={true}
                               defaultValue={state.form?.ip}/>
                    <TextInput name="port"
                               label=":"
                               isRequired={true}
                               defaultValue={state.form?.port.toString()}/>
                    <TextInput name="description"
                               label=":"
                               isRequired={true}
                               defaultValue={state.form?.description}/>
                    <TextInput name="userName"
                               label=" :"
                               isRequired={true}
                               defaultValue={state.form?.userName}/>
                    <TextInput name="password"
                               label=":"
                               isRequired={true}
                               defaultValue={state.form?.password}/>
                </ModalBody>
                <ModalFooter>
                    <Button variant="dark" type="submit"></Button>
                    <Button variant="secondary" onClick={closeLastModal}></Button>
                </ModalFooter>
            </Form>
        </FormProvider>

    </>
})
