import {observer} from "mobx-react-lite";
import {Button, Image, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {changeLanguage, getTranslate} from "../../../utils/localization";
import React from "react";
import {closeLastModal} from "../../../component/modal/modalStore";
import {useNavigate} from "react-router-dom";

export const LanguageModal = observer(() => {
    const navigate = useNavigate()
    const options = [
        {value: "ru-ru", link: `${window.location.origin}/img/flags/ru-ru.svg`, label: ""},
        {value: "en-us", link: `${window.location.origin}/img/flags/en-us.svg`, label: "English"},
        {value: "zh-ch", link: `${window.location.origin}/img/flags/zh-ch.svg`, label: ""},
        // {value: "zh-ch", label: <div className="text-center">
        //         <img width={32} height={32} src={`${window.location.origin}/img/flags/zh-ch.svg`}/>
        //     </div>
        // }
    ]

    return <>
        <ModalHeader>
            <ModalTitle>{getTranslate(" ")}</ModalTitle>
        </ModalHeader>
        <ModalBody>
            <div className="d-flex">
                <Button variant="light" className="me-2" onClick={() => {
                    changeLanguage(options[0].value)
                    navigate(0)
                }}>
                    <span className="me-1">
                        <Image src={options[0].link} width={32} height={32} fluid={true}/>
                    </span>
                    <span>
                        {options[0].label}
                    </span>
                </Button>
                <Button variant="light" className="me-2" onClick={() => {
                    changeLanguage(options[1].value)
                    navigate(0)
                }}>
                    <span className="me-1">
                        <Image src={options[1].link} width={32} height={32} fluid={true}/>
                    </span>
                    <span>
                        {options[1].label}
                    </span>
                </Button>
                <Button variant="light" className="me-2" disabled={true} onClick={() => {
                    changeLanguage(options[2].value)
                    navigate(0)
                }}>
                    <span className="me-1">
                        <Image src={options[2].link} width={32} height={32} fluid={true}/>
                    </span>
                    <span>
                        {options[2].label}
                    </span>
                </Button>
            </div>
        </ModalBody>
        <ModalFooter>
            <Button variant="dark" onClick={closeLastModal}>{getTranslate("")}</Button>
        </ModalFooter>
    </>
})