import {RouteObject} from "react-router-dom";

import {Index as LogIndex} from "../../../page/admin/logs/Index"
import {Index as LocationIndex} from "../../../page/admin/location/Index"
import {Index as UsersIndex} from "../../../page/admin/users/Index"
import {Index as TicketManagementList} from "../../../page/admin/ticket-management/ticket-list/Index"
import {Index as TicketManagementView} from "../../../page/admin/ticket-management/ticket-view/Index"
import {Index as FinanceIndex} from "../../../page/admin/finance/Index"
import {Index as FileManagerIndex} from "../../../page/admin/file/Index"
import {Index as StatisticsIndex} from "../../../page/admin/statistics/Index"
import {PrivateRoute} from "../component/PrivateRoute";
import {getTicketsAsync} from "../../../page/admin/ticket-management/ticket-list/store";
import {getTicketAsync} from "../../../page/admin/ticket-management/ticket-view/store";
import {getLocationListAsync} from "../../../page/admin/location/component/list/store";
import {getUsersAsync} from "../../../page/admin/users/components/list/store";
import {getPromoCodes} from "../../../page/admin/finance/components/promo-code/store";
import {getDirectoriesAsync} from "../../../page/admin/file/store";
import {getPositionsAsync} from "../../../page/admin/finance/components/access-list/store";
import {getStatisticsAsync} from "../../../page/admin/statistics/store";

export const adminRoute : RouteObject[] = [
    {
        path: "/admin/users",
        element: <PrivateRoute chiledern={<UsersIndex/>}/>,
        loader: async () => {
            await getUsersAsync()
            return null
        }
    },
    {
        path: "/admin/location",
        element: <PrivateRoute chiledern={<LocationIndex/>}/>,
        loader: async () => {
            await getLocationListAsync()
            return null
        }
    },
    {
        path: "/admin/logs",
        element: <PrivateRoute chiledern={<LogIndex/>}/>
    },
    {
        path: "/admin/files",
        element: <PrivateRoute chiledern={<FileManagerIndex/>}/>,
        loader: async () => {
            await getDirectoriesAsync()
            return null
        }
    },
    {
        path: "/admin/tickets",
        element: <PrivateRoute chiledern={<TicketManagementList/>}/>,
        loader: async () => {
            await getTicketsAsync()
            return null
        }
    },
    {
        path: "/admin/ticket/:ticketId",
        element: <PrivateRoute chiledern={<TicketManagementView/>}/>,
        loader: async ({params}) => {
            await getTicketAsync(params.ticketId!)
            return null
        }
    },
    {
        path: "/admin/finance",
        element: <PrivateRoute chiledern={<FinanceIndex/>}/>,
        loader: async () => {
            await getPromoCodes()
            await getPositionsAsync()
            return null
        }
    },
    {
        path: "/admin/statistics",
        element: <PrivateRoute chiledern={<StatisticsIndex/>}/>,
        loader: async () => {
            await getStatisticsAsync()
            return null
        }
    },
]
