import {action, observable} from "mobx";
import {getAsync} from "../../../common/fetch/getAsync";
import {ConnectionByLocation} from "./model/ConnectionByLocation";

let statisticsState = observable({
    registerToday: 0,
    registerYesterday: 0,
    totalUsers: 0,
    connectionByLocations: null as unknown as ConnectionByLocation[]
})

const getStatisticsAsync = action(async () => {
    await getAsync("/Admin/GetStatistics").then(response => {
        statisticsState = response.data
    })
})

export {statisticsState, getStatisticsAsync}