import {observer} from "mobx-react-lite";
import {state} from "../store";
import {useNavigate} from "react-router-dom";
import {Button} from "react-bootstrap";
import {PencilFill} from "react-bootstrap-icons";
import {openModal} from "../../../../../common/component/modal/modalStore";
import {EditTicket} from "./edit-ticket/EditTicket";
import {size} from "../../../../../common/component/modal/IModal";

export const TicketList = observer(() => {
    const navigate = useNavigate()

    return <div className="mt-3">
        <div className="d-none d-lg-flex text-center">
            <div className="w-100"></div>
            <div className="w-100"></div>
            <div className="w-100"></div>
            <div className="w-100"></div>
            <div className="w-100"></div>
            <div className="w-100"></div>
        </div>

        {
            state.tickets?.map(ticket => {
                return <div className="d-flex flex-column flex-md-row">
                    <div key={ticket.id} onClick={() => navigate(`/admin/ticket/${ticket.id}`)} className="d-flex align-items-center w-100 py-2 text-center" style={{cursor: "pointer"}}>
                        <div className="w-100">{ticket.id}</div>
                        <div className="w-100">{ticket.title}</div>
                        <div className="w-100">{ticket.category}</div>
                        <div className="w-100">{ticket.userName}</div>
                        <div className="w-100">
                            {new Date(ticket.createDate).toLocaleDateString()}
                        </div>
                        <div className="w-100">
                            <Button onClick={(event) => {
                                openModal({modal: <EditTicket id={ticket.id}/>, size: size.lg})
                                event.stopPropagation()
                            }}>
                                <PencilFill/>
                            </Button>
                        </div>
                    </div>
                </div>

            })
        }
    </div>
})
