import {Badge, FormGroup, FormLabel} from "react-bootstrap";
import {useFormContext} from "react-hook-form";
import {Controller} from "react-hook-form";
import {ISelectInput} from "./ISelectInput";
import Select from "react-select";
import {ActionMeta} from "react-select";
import {useEffect} from "react";

export const SelectInput = (props: ISelectInput) => {
    const {control, setValue, formState: {errors}} = useFormContext()

    const isRequired: boolean = props.isRequired || false;
    const isMulti = props.isMulti || false;
    let closeMenuOnSelect = props.closeMenuOnSelect || true;

    useEffect(() => {
        setValue(props.name, props.defaultValue)
    }, [props.defaultValue]);

    return <FormGroup className="mb-2">
        {props.label &&
            <FormLabel>{props.label}</FormLabel>
        }
        <Controller
            name={props.name}
            control={control}
            rules={{ required: { message: '  ', value: isRequired } }}
            render={({ field }) => (
                <Select
                    {...field}
                    options={props.options}
                    isMulti={isMulti}
                    placeholder={props.placeholder}
                    defaultValue={props.defaultValue}
                    closeMenuOnSelect={closeMenuOnSelect}
                    isDisabled={props.isDisabled}
                    onChange={(newVal: any, actionMeta: ActionMeta<any>) => {
                        setValue(props.name, newVal);
                        if (props.onChange) props.onChange(newVal, actionMeta);
                    }}
                />
            )}
        />
        {errors[props.name]?.message &&
            <Badge bg="warning">{errors[props.name]?.message?.toString()}</Badge>
        }
    </FormGroup>
}
