import {observer} from "mobx-react-lite";
import {Button} from "react-bootstrap";
import {statePositions} from "./store";
import {Pencil, TrashFill} from "react-bootstrap-icons";
import {openModal} from "../../../../../common/component/modal/modalStore";
import {AddEditAccessPositionModal} from "./AddEditAccessPositionModal";

export const AccessList = observer(() => {
    return <div className="w-100 me-4">
        <div>
            <div className="d-flex justify-content-between">
                <h5>:</h5>
                <Button variant="dark"
                        size="sm">
                    
                </Button>
            </div>
            <hr/>
        </div>
        <div>
            {
                statePositions.positions?.map(position => {
                    return <div className="d-flex mb-1">
                        <div className="w-100">{position.name}</div>
                        <div className="w-100">{position.price}</div>
                        <div className="d-flex">
                            <Button variant="danger" className="me-2" size="sm">
                                <TrashFill/>
                            </Button>
                            <Button variant="dark"
                                    size="sm"
                                    onClick={() => openModal({modal: <AddEditAccessPositionModal id={position.id}/>, size: undefined})}>
                                <Pencil/>
                            </Button>
                        </div>
                    </div>
                })
            }
        </div>
    </div>
})