import {Badge, Button} from "react-bootstrap";
import {state} from "../store";
import {observer} from "mobx-react-lite";
import {openModal} from "../../../../common/component/modal/modalStore";
import {PaymentModal} from "./payment-modal/PaymentModal";
import {size} from "../../../../common/component/modal/IModal";
import {getTranslate} from "../../../../common/utils/localization";

export const Profile = observer(() => {
    return <div>
        <div className="d-block d-lg-flex justify-content-around">
            <div className="flex-fill">
                <div>
                    <span className="fw-bold-light">{getTranslate("")}:</span> {state.state?.login}
                </div>

                <div>
                    <span className="fw-bold-light">{getTranslate(" ")}: </span>{state.state?.email}
                </div>
                <div>
                    <span className="fw-bold-light">{getTranslate(" ")}: </span>{new Date(state.state?.registerDate).toLocaleDateString()}
                </div>
            </div>
            <div className="flex-fill">
                <div>
                    <span className="fw-bold-light">{getTranslate(" ")}: </span>{state.state?.balance}
                </div>
                <div>
                    <span className="fw-bold-light">{getTranslate("")}: </span>

                    {state.state?.isExpired &&
                        <Badge bg="secondary">{getTranslate(" ")}</Badge>
                    }

                    {!state.state?.isExpired &&
                        <Badge bg="success">{getTranslate("  ")} {new Date(state.state?.endAccessDate).toLocaleDateString()}</Badge>
                    }

                </div>
            </div>
        </div>
        <div className="flex-column d-lg-flex justify-content-between mt-3">
            <div className="d-flex justify-content-around justify-content-lg-start">
                <Button variant="dark"
                        size="sm" onClick={() => openModal({modal: <PaymentModal/>, size: size.lg})}>
                    {state.state?.isExpired ? getTranslate("  ") : getTranslate(" ")}
                </Button>
            </div>
        </div>
        <hr/>
    </div>
})
