import {action, observable} from "mobx";
import {getAsync} from "../../../../common/fetch/getAsync";
import {postAsync} from "../../../../common/fetch/postAsync";

const state = observable({view: null as unknown as ITicketView})

const getTicketAsync = action(async (id: string) => {
    getAsync(`/Admin/GetViewTicketManagement?ticketId=${id}`).then(response => {
        state.view = response.data
    })
})

const addMessageAsync = action(async (ticketId: string, message: string) => {
    postAsync("/Admin/AddTicketMessage", {
        ticketId: ticketId,
        message: message
    }, () => {
        getTicketAsync(ticketId)
    })
})

export {getTicketAsync, addMessageAsync, state}

interface ITicketView{
    id: number,
    title: string,
    ticketMessages: IMessage[]
}

interface IMessage{
    id: number,
    message: string,
    userId: number,
    sendDate: string
}
