import {Alert} from "react-bootstrap";
import {Navigate, useNavigate, useSearchParams} from "react-router-dom";
import {observer} from "mobx-react-lite";
import React, {useEffect} from "react";
import {sendActivation, state} from "./store";
import {authState} from "../../../common/store/auth";

export const Index = observer(() => {
    const [searchParams] = useSearchParams()
    const guid = searchParams.get("guid")
    const navigation = useNavigate()

    useEffect(() => {
        sendActivation(guid || "")
    }, []);

    useEffect(() => {
        if(state.activationIsSuccess){
            setTimeout(() => navigation("/login"), 3000)
        }else if(!state.activationIsSuccess && state.activationIsSuccess != null){
            setTimeout(() => navigation("/"), 3000)
        }
    }, [state.activationIsSuccess]);

    if(authState.token !== null)
        return <Navigate to="/"/>

    return <div className="d-flex justify-content-center align-items-center w-100 vh-100">
        {state.activationIsSuccess &&
            <Alert variant="light">
                   .      .
            </Alert>
        }

        {!state.activationIsSuccess && state.activationIsSuccess != null &&
            <Alert variant="danger">
                 .       .
            </Alert>
        }
    </div>
})
