import {observer} from "mobx-react-lite";
import {state} from "../store";
import {Button, FormControl, InputGroup} from "react-bootstrap";
import {copyLink} from "../../referral-program/store";
import {ArrowRightSquareFill, ClipboardFill} from "react-bootstrap-icons";
import {useRef} from "react";
import {useNavigate} from "react-router-dom";
import {getTranslate} from "../../../../common/utils/localization";

export const Referrals = observer(() => {
    const referralLink = useRef<HTMLInputElement>(null);
    const navigate = useNavigate();

    return <div className="w-100 px-2">
        <div>
            <div className="d-flex mb-3 justify-content-between">
                <h5>{getTranslate(" ")}:</h5>
                <div style={{cursor: "pointer"}} onClick={() => navigate("/referral")} title="">
                    <ArrowRightSquareFill width={25} height={25} color="#b5b5b5"/>
                </div>
            </div>

            <InputGroup className="mb-lg-0 mb-3">
                <FormControl ref={referralLink} aria-describedby="with-copy-btn" value={state.state?.referralLink} disabled/>
                <Button id="with-copy-btn" variant="dark" onClick={() => copyLink(referralLink.current!.value)}>
                    <ClipboardFill width={16} height={16}/>
                </Button>
            </InputGroup>
            <hr className="d-lg-block d-none"/>
        {state.state?.referrals.length > 0 &&
            state.state?.referrals.map(referral => {
                    return <div className="d-flex mb-2 text-center" key={referral.id}>
                        <div className="flex-fill">{referral.login}</div>
                        <div className="flex-fill">{referral.topupTotal}</div>
                    </div>
                }
            )
        }

        {
            state.state?.referrals.length <= 0 &&
                <div className="text-center">
                    {getTranslate("    10%     ")}.
                </div>
        }
        </div>
    </div>
})