import {Button} from "react-bootstrap";
import {TicketList} from "./component/TicketList";
import {Index as AddEditForm} from "./component/add-ticket-modal/Index";
import {openModal} from "../../../../common/component/modal/modalStore";
import {observer} from "mobx-react-lite";
import {userTicketsState} from "./store";
import {size} from "../../../../common/component/modal/IModal";

export const Index = observer(() => {

    return <>
        <div>
            <h4></h4>
        </div>
        <hr className="mt-0"/>
        <div>
            <Button variant="dark"
                    onClick={() => openModal({modal: <AddEditForm/>, size: size.lg})}>
                 
            </Button>
        </div>

        {userTicketsState.list?.length <= 0 &&
            <div className="w-100 text-secondary text-center mt-3">  </div>
        }

        {userTicketsState.list?.length > 0 &&
            <TicketList/>
        }
    </>
})
