import {FormProvider, useForm} from "react-hook-form";
import {Button, Form, FormGroup} from "react-bootstrap";
import {TextInput} from "../../../common/component/input/text/TextInput";
import React, {useRef} from "react";
import {SetPasswordInputs} from "../../../common/component/input/set-password/SetPasswordInputs";
import {sendForm} from "./store";
import {mainStore} from "../../../common/mainStore";
import {Link, Navigate, useNavigate, useSearchParams} from "react-router-dom";
import {authState} from "../../../common/store/auth";
import {SuccessRegister} from "./component/SuccessRegister";
import {observer} from "mobx-react-lite";
import {state as registerState} from "./store"
import HCaptcha from '@hcaptcha/react-hcaptcha';
import {getTranslate} from "../../../common/utils/localization";

export const Index = observer(() => {
    const hookForm = useForm()
    const navigation = useNavigate()
    const [searchParams] = useSearchParams()
    const captchaRef = useRef<HCaptcha>(null)


    if(authState.token !== null)
        return <Navigate to="/"/>

    if(registerState.isRegisterSuccess)
        return <SuccessRegister/>

    return <div>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit(sendForm)}>
                <div className="d-flex flex-column justify-content-center align-items-center vh-100">
                    <Link to="/" className="text-center text-decoration-none text-black mb-4">
                        <h1 className="fw-bold">LockVPN</h1>
                        <h5>{getTranslate(" ")}</h5>
                    </Link>

                    <div className="d-flex flex-column align-items-center login-form">
                        <TextInput name="login"
                                   label={`${getTranslate("")}:`}
                                   placeholder=" " isRequired={true}/>
                        <TextInput name="email"
                                   label={`${getTranslate(" ")}:`}
                                   placeholder={getTranslate("  ")}
                                   isRequired={true}/>
                        <SetPasswordInputs isRequired={true}/>
                        <TextInput name="token"
                                   attributes={{className: "d-none"}}
                                   isRequired={true}/>

                        <TextInput name="guid"
                                   defaultValue={searchParams.get("parent") || null}
                                   attributes={{className: "d-none"}}/>

                        {mainStore.captchaPublicKey &&
                            <FormGroup className="text-center">
                                <HCaptcha ref={captchaRef}
                                    sitekey={mainStore.captchaPublicKey}
                                    onVerify={(token) => hookForm.setValue("token", token)}
                                />
                            </FormGroup>
                        }

                        <Button type="submit" variant="dark" className="mt-2" onClick={captchaRef.current?.resetCaptcha}>
                            {getTranslate(" ")}
                        </Button>
                        <Button type="submit" variant="secondary" className="mt-2" onClick={() => navigation("/Login")}>
                            {getTranslate("  ")}
                        </Button>
                    </div>
                </div>
            </Form>
        </FormProvider>
    </div>
})
