import {createBrowserRouter} from "react-router-dom";
import { Index as LoginIndex } from "../../page/user/login/Index";
import { Index as ExtensionAuthIndex } from "../../page/user/extension-auth/Index";
import { Index as PaymentIndex } from "../../page/user/payment/Index";
import {Layout} from "./layout/Layout";
import {userRoute} from "./route/userRoute";
import {adminRoute} from "./route/adminRoute";
import {Index as RegisterIndex} from "../../page/user/register/Index";
import {Index as ActivationIndex} from "../../page/user/activation/Index";
import {Index as ResetPasswordIndex} from "../../page/user/reset-password/Index";

export const Routing = createBrowserRouter([
    {
        path: "/",
        element: <Layout/>,
        children: [...userRoute, ...adminRoute]
    },
    {
        path: "/login",
        element: <LoginIndex/>
    },
    {
        path: "/extension-login",
        element: <ExtensionAuthIndex/>
    },
    {
        path: "/payment",
        element: <PaymentIndex/>
    },
    {
        path: "/register",
        element: <RegisterIndex/>
    },
    {
        path: "/activation",
        element: <ActivationIndex/>
    },
    {
        path: "/reset-password",
        element: <ResetPasswordIndex/>
    }

])
