import {action, observable} from "mobx";
import {postAsync} from "../../../common/fetch/postAsync";

const isLinkCreat= observable({value: false})
const isNewPasswordSet = observable({value: false})

const createResetPasswordLinkAsync = action(async (form: any) => {
    await postAsync("/User/CreateResetPasswordLink", form, (response) => {
        isLinkCreat.value = response.data
    })
})

const setNewPasswordAsync = action(async (form: any) => {
    const md5 = require('md5');

    form.password = md5(form.password)

    await postAsync("/User/SetNewPassword", form, (response) => {
        isNewPasswordSet.value = response.data
    })
})

export { isLinkCreat, createResetPasswordLinkAsync, isNewPasswordSet, setNewPasswordAsync }