import {action, observable} from "mobx";

const localizationState = observable({
    lang: localStorage.getItem("localization") || "ru-ru"
})

const changeLanguage = action((lang: string) => {
    localizationState.lang = lang
    localStorage.setItem("localization", lang)
})

const getTranslate = action((key: string) : string => {
    const localization: {[key: string]: string} = require(`./lang/${localizationState.lang}.json`);

    if(localizationState.lang === "en-us")
    {
        return localization[key] || key
    }
    else
    {
        return key
    }


})

export {localizationState, getTranslate, changeLanguage}