import axios, {AxiosResponse} from "axios";
import {errorToast} from "../toast/error";
import {action} from "mobx";
import {authState} from "../store/auth";

export const postAsync = action(async <TData>(url: string, data: TData, afterPost: (response: AxiosResponse<any, any>) => void) => {
    await axios.post(url, data, {
        headers: {
            'Authorization': 'Bearer ' + authState.token
        }
    })
    .then(response => {
        afterPost(response);
    }).catch(reason => {
        errorToast(reason.response.data.Message)
    })
})
