import {observer} from "mobx-react-lite";
import {getTranslate} from "../../../../../common/utils/localization";
import {FormProvider, useForm} from "react-hook-form";
import {Button, Form, FormControl, FormGroup, FormLabel} from "react-bootstrap";
import {SetPasswordInputs} from "../../../../../common/component/input/set-password/SetPasswordInputs";
import {TextInput} from "../../../../../common/component/input/text/TextInput";
import {addChangeEmailRequestAsync, addChangePasswordRequestAsync, deattachTelegram, settingsState} from "../../store";
import {Plug, Plugin, SendCheckFill, SendSlashFill} from "react-bootstrap-icons";

export const Profile = observer(() => {
    const passwordHookForm = useForm()
    const emailHookForm = useForm()

    return <>
        <div>
            <h6>{getTranslate("")}</h6>
            <hr className="mt-1"/>
        </div>
        <FormProvider {...passwordHookForm}>
            <Form onSubmit={passwordHookForm.handleSubmit(addChangePasswordRequestAsync)}>
                <div className="d-block d-lg-flex justify-content-around align-items-end mb-3">
                    <SetPasswordInputs
                        isRequired={true}
                        attributes={{className: "w-100 mx-2"}}/>
                    <Button variant="dark" type="submit">
                        {getTranslate("")}
                    </Button>
                </div>
            </Form>
        </FormProvider>

        <FormProvider {...emailHookForm}>
            <Form onSubmit={emailHookForm.handleSubmit(addChangeEmailRequestAsync)}>
                <div className="d-block d-lg-flex justify-content-around align-items-end mb-3">
                    <FormGroup className="w-100 mx-2">
                        <FormLabel>{getTranslate(" ")}:</FormLabel>
                        <FormControl disabled={true} value={settingsState.email}/>
                    </FormGroup>
                    <TextInput name="email"
                               label={`${getTranslate("  ")}:`}
                               placeholder={getTranslate("  ")}
                               attributes={{className: "w-100 mx-2"}} isRequired={true}/>
                    <Button variant="dark" type="submit">
                        {getTranslate("")}
                    </Button>
                </div>
            </Form>
        </FormProvider>
        <div className="d-block d-lg-flex justify-content-start align-items-end mb-3">
            <FormGroup className="mx-2">
                <FormLabel>{getTranslate("Telegram ID")}:</FormLabel>
                <FormControl disabled={true} value={settingsState.telegramId == "0" ? "" : settingsState.telegramId}/>
            </FormGroup>
            <div>
                {settingsState.telegramId == "0" &&
                    <Button variant="dark">
                        <SendCheckFill/>
                    </Button>
                }

                {settingsState.telegramId != "0" &&
                    <Button variant="dark" onClick={deattachTelegram}>
                        <SendSlashFill/>
                    </Button>
                }
            </div>
        </div>
    </>
})