import {Badge, FormControl, FormGroup, FormLabel} from "react-bootstrap";
import {INumberInput} from "./INumberInput";
import {useFormContext} from "react-hook-form";
import {useEffect} from "react";

export const NumberInput = (props: INumberInput) => {
    const {register, setValue, formState: {errors}} = useFormContext()
    const isRequired = props.isRequired || false;
    const minValue = props.minValue || Number.MIN_VALUE;
    const maxValue = props.maxValue || Number.MAX_VALUE;

    useEffect(() => {
        setValue(props.name, props.defaultValue);
    }, [props.defaultValue]);

    return <FormGroup className="mb-2">
        {props.label &&
            <FormLabel>{props.label}</FormLabel>
        }

        <FormControl {...register(props.name, {
            required: {value: isRequired, message: "   ."},
            validate: (value) => {
                if(isNaN(Number(value)))
                    return " "
            },
            min: {value: minValue, message: "   "},
            max: {value: maxValue, message: "   "}
        })} placeholder={props.placeholder}/>

        {errors[props.name]?.message &&
            <Badge bg="warning" className="mt-2">{errors[props.name]?.message?.toString()}</Badge>
        }
    </FormGroup>
}