import {observer} from "mobx-react-lite";
import React from "react";
import {useSearchParams} from "react-router-dom";
import {MainContent} from "./component/MainContent";
import {ContactModal} from "./modal/contacts/ContactModal";
import {TopMenu} from "./component/TopMenu";
import {GooglePlay} from "./page/GooglePlay";
import {openModal} from "../../../component/modal/modalStore";
import {PrivacyPolicyModal} from "./modal/privacy-policy/PrivacyPolicyModal";
import {size} from "../../../component/modal/IModal";
import {TermsOfReturnModal} from "./modal/terms-of-return/TermsOfReturnModal";
import {OfferAgreementModal} from "./modal/offer-agreement/OfferAgreementModal";
import {Telegram} from "react-bootstrap-icons";
import {getTranslate} from "../../../utils/localization";

export const Index = observer(() => {
    const [searchParams, setSearchParams] = useSearchParams()
    const page = searchParams.get("page")

    return <div className="d-flex flex-column justify-content-between bg-second" style={{minHeight: "100vh"}}>
        <header className="d-flex justify-content-around align-items-center top-menu bg-second" style={{height: "50px"}}>
            <TopMenu/>
        </header>

        <main className="d-flex flex-column justify-content-around align-items-center text-center bg-second">
            {page !== "google-play" && <MainContent/>}
            {page === "google-play" && <GooglePlay/>}
        </main>

        <footer className="d-flex justify-content-around bg-white" style={{height: "50px"}}>
            <div className="d-md-flex d-block">
                <div className="d-flex align-items-center px-3 h-100 text-decoration-none horizontal-menu-item"
                     style={{cursor: "pointer"}}
                     onClick={() => openModal({modal: <PrivacyPolicyModal/>, size: size.xl})}>
                    {getTranslate(" ")}
                </div>
                <div className="d-flex align-items-center px-3 h-100 text-decoration-none horizontal-menu-item"
                     style={{cursor: "pointer"}}
                     onClick={() => openModal({modal: <TermsOfReturnModal/>, size: size.xl})}>
                    {getTranslate(" ")}
                </div>
                <div className="d-flex align-items-center px-3 h-100 text-decoration-none horizontal-menu-item"
                     style={{cursor: "pointer"}}
                     onClick={() => openModal({modal: <OfferAgreementModal/>, size: size.xl})}>
                    {getTranslate(" ")}
                </div>
                <div className="d-flex align-items-center px-3 h-100 text-decoration-none horizontal-menu-item"
                     style={{cursor: "pointer"}}
                     onClick={() => openModal({modal: <ContactModal/>, size: size.lg})}>
                    {getTranslate("")}
                </div>
            </div>

            <div className="d-block d-lg-flex mt-lg-0 mt-2">
                <div className="d-flex align-items-center">
                    <a href="https://appgallery.huawei.com/app/C110194213"
                       className="text-decoration-none border border-2 rounded-2 me-2"
                       target="_blank" rel="noreferrer">
                        <img src={`${window.location.origin}/img/appgallery-icon.svg`}
                             alt="App Gallery"
                             width={30}
                             height={30}
                             color="white"/>
                    </a>
                    <a href="https://play.google.com/store/apps/details?id=com.lockvpnandroidapp"
                       className="text-decoration-none border border-2 rounded-2 me-2"
                       target="_blank" rel="noreferrer">
                        <img src={`${window.location.origin}/img/google-play-icon.svg`}
                             alt="App Gallery"
                             width={30}
                             height={30}
                             color="white"/>
                    </a>
                </div>

                <div className="d-flex justify-content-between align-items-center mt-lg-0 mt-3">
                    <a href="https://t.me/lockvpn_blog"
                       target="_blank"
                       rel="noreferrer"
                       className="d-flex align-items-center px-1 px-lg-3 h-100 text-decoration-none text-white horizontal-menu-item">
                        <Telegram className="me-2" width={25} height={25} color="black"/>
                    </a>
                </div>
            </div>
        </footer>
    </div>
})
