import {action, observable} from "mobx";
import axios, {AxiosResponse} from "axios";
import {UserState} from "./model/UserState";
import {postAsync} from "../../../common/fetch/postAsync";
import {successToast} from "../../../common/toast/success";
import {setToken} from "../../../common/store/auth";
import {googleLogout} from "@react-oauth/google";

const state = observable({loginForm: null as unknown as ILoginForm});

const authAsync = action(async (form: any) => {
    const md5 = require('md5');
    form.password = md5(form.password)

    const afterPost = async (response: AxiosResponse<any, any>) => {

        setToken(response.data);
        localStorage.setItem("token", response.data);

        successToast("    .")
    }

    await postAsync<Object>("/User/Login", form, afterPost)

})

const authWithGoogle = action(async (token: string) => {
    postAsync("/User/AuthWithGoogle", {acceptToken: token}, (response) => {
        setToken(response.data);
        localStorage.setItem("token", response.data);

        successToast("    .")
    })
})

export {state, authAsync, authWithGoogle}

interface ILoginForm{
    state: UserState
}
