import {observer} from "mobx-react-lite";
import {Button, Form, FormControl} from "react-bootstrap";
import {copyLink, createDirectoryAsync, deleteDirectoryAsync, deleteFileAsync, state, uploadFileAsync} from "./store";
import {FormProvider, useForm} from "react-hook-form";
import {TextInput} from "../../../common/component/input/text/TextInput";
import {ClipboardFill, Download, TrashFill, Upload} from "react-bootstrap-icons";
import {useRef} from "react";

export const Index = observer(() => {
    const hookForm = useForm()
    const fileInputRef = useRef<HTMLInputElement>(null);

    return <>
        <div className="d-flex">
            <FormProvider {...hookForm}>
                <Form className="d-flex align-items-center" onSubmit={hookForm.handleSubmit(createDirectoryAsync)}>
                    <TextInput name="name" isRequired={true}/>
                    <div>
                        <Button variant="dark" type="submit" size="sm">
                             
                        </Button>
                    </div>

                </Form>
            </FormProvider>

            <div>
                <FormControl type="file" ref={fileInputRef} onChange={uploadFileAsync} className="d-none"/>
            </div>
        </div>
        <hr/>
        <div>
            {
                state.value?.directories.map(directory => {
                    return <>
                        <div className="d-flex w-100 p-2 my-1 bg-light">
                            <div className="w-100">
                                {directory.name}
                            </div>
                            <div>
                                <Button size="sm" variant="secondary" className="me-2" onClick={() => {
                                    state.value.uploadPath = directory.path
                                    fileInputRef.current?.click()
                                }}>
                                    <Upload/>
                                </Button>
                            </div>
                            <div>
                                <Button size="sm" variant="danger" onClick={() => deleteDirectoryAsync(directory.path)}>
                                    <TrashFill/>
                                </Button>
                            </div>
                        </div>
                        {
                            directory.files.map(file => {
                                return <div className="d-flex w-100 p-2">
                                    <div className="w-100">{file}</div>
                                    <div className="d-flex">
                                        <Button size="sm" className="me-2" variant="dark" onClick={() => copyLink(`${window.location.origin}/files/${directory.name}/${file}`)}>
                                            <ClipboardFill/>
                                        </Button>
                                        <a href={`/files/${directory.name}/${file}`} className="btn btn-sm btn-secondary me-2">
                                            <Download/>
                                        </a>
                                        <Button size="sm" variant="danger" onClick={async () => await deleteFileAsync(directory.path, file)}>
                                            <TrashFill/>
                                        </Button>
                                    </div>
                                </div>
                            })
                        }
                    </>

                })
            }
        </div>
    </>
})