import {observer} from "mobx-react-lite";
import {state} from "../store";
import {authState} from "../../../../../common/store/auth";

export const MessageList = observer(() => {
    return <div className="ticket-message-list">
        {
            state.view?.ticketMessages?.map(message => {
                const bg = message.userId === Number(authState.user.id) ? "bg-second text-white" : "bg-light";

                return <div key={message.id} className={`my-2 w-100 ${bg} p-2 border rounded-2 border-0`}>
                    {message.message}
                </div>
            })
        }
    </div>
})
