import {action, observable} from "mobx";
import {ILog} from "./model/ILog";
import {postAsync} from "../../../common/fetch/postAsync";
import {getAsync} from "../../../common/fetch/getAsync";
import {successToast} from "../../../common/toast/success";

const state = observable({state: null as unknown as ILog})

const getLogsAsync = action(async () => {
    await getAsync("/Admin/GetLogs")
        .then(response => state.state = response.data);
})

const deleteLog = action(async (id: number) => {
    await postAsync("/Admin/DeleteLog", {id:id}, () => {
        successToast("  ")
        getLogsAsync()
    })
})

export {state, getLogsAsync, deleteLog}
