import {IOption} from "../../../../../../common/model/option";
import {action, observable} from "mobx";
import {getAsync} from "../../../../../../common/fetch/getAsync";
import {postAsync} from "../../../../../../common/fetch/postAsync";
import {closeLastModal} from "../../../../../../common/component/modal/modalStore";
import {getUserTickets} from "../../store";

const state = observable({form: null as unknown as IAddForm});

const getAddFormAsync = action(async () => {
    await getAsync("/Ticket/GetAddTicketForm").then(response => state.form = response.data)
})

const createTicketAsync = async (form: any) => {
    form.categoryId = form.categoryId.value;
    await postAsync("/Ticket/CreateTicket", form, () => {
        closeLastModal()
        getUserTickets()
    })
}

export {state, getAddFormAsync, createTicketAsync}

interface IAddForm{
    categories: IOption[]
}
