import {observer} from "mobx-react-lite";
import {LeftMenu} from "./LeftMenu";

import "./style.css"
import {Content} from "./Content";
import {authState} from "../../store/auth";
import {Index} from "./guest/Index";
import React from "react";
import {ModalProvider} from "../../component/modal/ModalProvider";
import {useSearchParams} from "react-router-dom";
import {openModal} from "../../component/modal/modalStore";
import {PrivacyPolicyModal} from "./guest/modal/privacy-policy/PrivacyPolicyModal";
import {size} from "../../component/modal/IModal";
import {ContactModal} from "./guest/modal/contacts/ContactModal";
import {OfferAgreementModal} from "./guest/modal/offer-agreement/OfferAgreementModal";
import {TermsOfReturnModal} from "./guest/modal/terms-of-return/TermsOfReturnModal";
import {ConfirmChangePasswordRequest} from "../../component/handler/ConfirmChangePasswordRequest";
import {ConfirmChangeEmailRequest} from "../../component/handler/ConfirmChangeEmailRequest";
import {mainStore} from "../../mainStore";
import {Loading} from "../../component/Loading";

export const Layout = observer(() => {
    const [searchParams, setSearchParams] = useSearchParams()
    const page = searchParams.get("page")

    if(page === "privacy_policy")
        openModal({modal: <PrivacyPolicyModal/>, size: size.xl})

    if(page === "offer_agreement")
        openModal({modal: <OfferAgreementModal/>, size: size.xl})

    if(page === "terms_of_return")
        openModal({modal: <TermsOfReturnModal/>, size: size.xl})

    if(page === "contacts")
        openModal({modal: <ContactModal/>, size: undefined})

    if(authState.token === null)
        return <>
            <Index/>
            <ModalProvider/>
            <ConfirmChangePasswordRequest/>
            <ConfirmChangeEmailRequest/>
            <Loading/>
        </>


    return <>
        <div className="d-flex justify-content-center vh-100">
            <LeftMenu/>
            <Content/>
        </div>
        <ModalProvider/>
        <ConfirmChangePasswordRequest/>
        <ConfirmChangeEmailRequest/>
        <Loading/>
    </>

})
