import {action, observable} from "mobx";
import {IUser} from "./models";
import {getAsync} from "../../../../../common/fetch/getAsync";
import {postAsync} from "../../../../../common/fetch/postAsync";
import {successToast} from "../../../../../common/toast/success";

const state = observable({
    users: null as unknown as IUser[],
    count: 0
})

const getUsersAsync = action(async () => {
    await getAsync("/Admin/GetUsers").then(response => {
        state.users = response.data.users
        state.count = response.data.count
    })
})

const deleteUserAsync = action(async (id: number) => {
    await postAsync("/Admin/DeleteUser", {userId: id}, (response) => {
        if(response.status === 200){
            getUsersAsync();
            successToast("  .")
        }
    })
})

export {state, getUsersAsync, deleteUserAsync}