import {observer} from "mobx-react-lite";
import {userTicketsState} from "../store";
import {Link} from "react-router-dom";

export const TicketList = observer(() => {
    return <div className="mt-3">
        <div className="d-none d-sm-flex justify-content-between text-center mb-1 border-bottom border-1 pb-1">
            <div className="w-100"></div>
            <div className="w-100"></div>
            <div className="w-100"></div>
            <div className="w-100"></div>
        </div>

        {userTicketsState.list &&
            userTicketsState.list.map(ticket => {
                return <Link key={ticket.id} to={`/ticket/${ticket.id}`} className="d-block d-sm-flex justify-content-between text-center py-2 text-decoration-none text-black">
                    <div className="w-100">{ticket.id}</div>
                    <div className="w-100">{ticket.title}</div>
                    <div className="w-100">{new Date(ticket.createDate).toLocaleDateString()}</div>
                    <div className="w-100">
                        {ticket.condition === 1 &&
                            <>
                                
                            </>
                        }

                        {ticket.condition === 2 &&
                            <>
                                
                            </>
                        }
                    </div>
                </Link>

            })
        }

{
    !userTicketsState.list &&
    <div className="w-100 text-center">    </div>
}

</div>
})
