import {observer} from "mobx-react-lite";
import {Button} from "react-bootstrap";
import {openModal} from "../../../../../common/component/modal/modalStore";
import {AddEditPromoCodeModal} from "./AddEditPromoCodeModal";
import {deletePromoCodeAsync, stateList} from "./store";
import {Pencil, TrashFill} from "react-bootstrap-icons";

export const PromoCodeList = observer(() => {
    return <div className="w-100 me-4">
        <div>
            <div className="d-flex justify-content-between">
                <h5>:</h5>
                <Button variant="dark"
                        size="sm"
                        onClick={() => openModal({modal: <AddEditPromoCodeModal id={0}/>, size: undefined})}>
                    
                </Button>
            </div>
            <hr/>
        </div>
        <div>
            {
                stateList.list?.map(promoCode => {
                    const startDate = new Date(promoCode.startDate).toLocaleDateString();
                    const endDate = new Date(promoCode.endDate).toLocaleDateString();

                    return <div className="d-flex align-items-center" key={promoCode.id}>
                        <div className="w-100">{promoCode.code}</div>
                        <div className="w-100">{promoCode.discount}</div>
                        <div className="w-100">{promoCode.usageCount}</div>
                        <div className="w-100">{`${startDate} - ${endDate}`}</div>
                        <div className="w-100">
                            <Button variant="danger"
                                    size="sm"
                                    className="me-2"
                                    onClick={() => deletePromoCodeAsync(promoCode.id)}>
                                <TrashFill/>
                            </Button>
                            <Button variant="dark"
                                    size="sm"
                                    onClick={() => openModal({modal: <AddEditPromoCodeModal id={promoCode.id}/>, size: undefined})}>
                                <Pencil/>
                            </Button>
                        </div>
                    </div>
                })
            }
        </div>
    </div>
})