import {action, observable} from "mobx";
import {getAsync} from "../../../../../common/fetch/getAsync";
import {postAsync} from "../../../../../common/fetch/postAsync";
import {authState, removeToken} from "../../../../../common/store/auth";
import {successToast} from "../../../../../common/toast/success";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";

const state = observable({
    user: null as unknown as {
        id: number,
        name: string,
        accessEndDate: string
    }
})

const getUserAsync = action(async (id: number) => {
    await getAsync(`/Admin/GetUser?id=${id}`).then(response => {
        state.user = response.data
    })
})

const updateAsync = action(async (form: any) => {

    if(form.password !== ""){
        const md5 = require('md5');
        form.password = md5(form.password)
    }

    await postAsync("/Admin/UpdateUser", form, () => {
        successToast(" ")
        closeLastModal()
    })
})

export {state, getUserAsync, updateAsync}