import {observer} from "mobx-react-lite";
import {List} from "./component/list/List";
import {Button} from "react-bootstrap";
import {openModal} from "../../../common/component/modal/modalStore";
import {AddEditCountry} from "./component/add-edit-country/AddEditCountry";
import {AddEditServer} from "./component/add-edit-server/AddEditServer";
import {size} from "../../../common/component/modal/IModal";

export const Index = observer(() => {
    return <>
        <div className="mb-3">
            <Button variant="dark"
                    className="me-2"
                    size="sm"
                    onClick={() => openModal({modal: <AddEditServer id={0}/>, size: size.lg})}>
                 
            </Button>
            <Button variant="dark"
                    size="sm"
                    onClick={() => openModal({modal: <AddEditCountry/>, size: size.lg})}>
                 
            </Button>
        </div>
        <hr/>
        <List/>
    </>
})
