import {observer} from "mobx-react-lite";
import {state} from "../store";
import {Badge} from "react-bootstrap";
import {getTranslate} from "../../../../common/utils/localization";

export const PaymentHistory = observer(() => {
    return <div className="w-100 px-2 mt-lg-0 mt-3">
        <div className="mb-3">
            <h5>{getTranslate(" ")}:</h5>
        </div>

        {state.state?.payments.length > 0 &&
            state.state?.payments.map(payment => {
                return <div className="d-flex mb-2" key={payment.id}>
                    <div className="w-100">{getTranslate(payment.range)}</div>
                    <div className="w-100 text-center">{new Date(payment.date).toLocaleDateString()}</div>
                    <div className="w-100 text-center">
                        {payment.state === 0 &&
                            <Badge bg="danger">{getTranslate(" ")}</Badge>
                        }

                        {payment.state === 1 &&
                            <Badge bg="success">{getTranslate("")}</Badge>
                        }
                    </div>
                </div>
            })
        }

        {state.state?.payments.length <= 0 &&
            <div>   .</div>
        }
    </div>
})
