import React from "react";
import "./style.css";
import {Link, Navigate, useNavigate, useSearchParams} from "react-router-dom";
import {Button, Form } from "react-bootstrap";
import {authState} from "../../../common/store/auth";
import {observer} from "mobx-react-lite";
import {FormProvider, useForm} from "react-hook-form";
import {TextInput} from "../../../common/component/input/text/TextInput";
import {PasswordInput} from "../../../common/component/input/password/PasswordInput";
import {authAsync, authWithGoogle} from "./store";
import { useGoogleLogin } from "@react-oauth/google";
import {Google} from "react-bootstrap-icons";
import {errorToast} from "../../../common/toast/error";
import {getTranslate} from "../../../common/utils/localization";

export const Index = observer(() => {
    const [searchParams, setSearchParams] = useSearchParams();
    const hookForm = useForm()
    const navigation = useNavigate()
    const loginWithGoogle = useGoogleLogin({
        onSuccess: async (response) => authWithGoogle(response.access_token),
        onError: () => errorToast(" ,  ")
    })

    const isWindows = searchParams.get("auth_type") === "windows"
    const isChrome = searchParams.get("auth_type") === "chrome"

    if(authState.token !== null){
        if(isChrome)
            document.location.href = "/extension-login"
        else if(isWindows)
            document.location.href = "/windows-login"
        else
            return <Navigate to="/"/>
    }

    return <div>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit((form) => authAsync(form))}
                  className={`d-flex flex-column align-items-center justify-content-center vh-100 ${isWindows ? "login-form-windows" : "login-form"}`}>
                <Link to="/" className="text-center text-decoration-none text-black mb-4">
                    <h1 className="fw-bold">LockVPN</h1>
                    <h5>{getTranslate(" ")}</h5>
                </Link>

                <TextInput label={`${getTranslate("")}:`}
                           name="login"
                           isRequired={true}
                           placeholder={`${getTranslate(" ")}`}/>
                <PasswordInput label={`${getTranslate("")}:`}
                               name="password"
                               isRequired={true}
                               placeholder={`${getTranslate(" ")}`}/>

                <Button variant="dark"
                        type="submit"
                        className="mt-2">
                    {getTranslate("")}
                </Button>

                <Button className="mt-2"
                        variant="light"
                        onClick={() => loginWithGoogle()}>
                    <Google className="me-2"/>
                    {getTranslate("  Google")}
                </Button>

                {!isWindows &&
                    <>
                        <Button variant="secondary" className="mt-3" onClick={() => navigation("/register")}>
                            {getTranslate("")}
                        </Button>
                        <Button variant="secondary" className="mt-2" onClick={() => navigation("/reset-password")}>
                            {getTranslate(" ")}
                        </Button>
                    </>
                }
            </Form>
        </FormProvider>
    </div>
})
