import {Profile} from "./component/Profile";
import {PaymentHistory} from "./component/PaymentHistory";
import {observer} from "mobx-react-lite";
import {useEffect} from "react";
import {Referrals} from "./component/Referrals";
import {useSearchParams} from "react-router-dom";
import {successToast} from "../../../common/toast/success";
import {getTranslate} from "../../../common/utils/localization";

export const Index = observer(() => {
    const [searchParams] = useSearchParams()

    useEffect(() => {
        if(searchParams.get("login_in") === "success_chrome")
            successToast("    ")
    }, [])

    return <div className="flex-fill">
        <div>
            <h4>{getTranslate("")}</h4>
        </div>
        <hr className="mt-0"/>
        <Profile/>
        <div className="d-lg-flex d-block">
            <Referrals/>
            <hr className="d-lg-none d-block"/>
            <PaymentHistory/>
        </div>

    </div>
})
