import {action, observable} from "mobx";
import {IAccessPosition} from "./IAccessPosition";
import {getAsync} from "../../../../../common/fetch/getAsync";
import {postAsync} from "../../../../../common/fetch/postAsync";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";
import {successToast} from "../../../../../common/toast/success";

const statePositions = observable({positions: null as unknown as IAccessPosition[]});
const statePosition = observable({positions: null as unknown as IAccessPosition});

const getPositionsAsync = action(async () => {
    await getAsync("/Admin/GetAccessPositions").then(response => {
        statePositions.positions = response.data
    })
})

const getPositionAsync = action(async (id: number) => {
    await getAsync(`/Admin/GetAccessPosition?id=${id}`).then(response => {
        statePosition.positions = response.data
    })
})

const updatePositionAsync = action(async (form: any) => {
    await postAsync("/admin/UpdateAccessPosition", form, (response) => {
        if (response.status === 200){
            successToast(" ")
            closeLastModal()
        }
    })
})

export { statePositions, statePosition, getPositionsAsync, getPositionAsync, updatePositionAsync }