import {useSearchParams} from "react-router-dom";
import {confirmChangeEmailAsync} from "../../mainStore";
import {useEffect} from "react";

export const ConfirmChangeEmailRequest = () => {
    const [searchParams, setSearchParams] = useSearchParams()

    useEffect(() => {
        if(searchParams.has("confirm-change-email-request-guid")){
            confirmChangeEmailAsync(searchParams.get("confirm-change-email-request-guid") || "")

            searchParams.delete("confirm-change-email-request-guid")
            setSearchParams(searchParams)
        }
    }, []);

    return <></>
}