import {action, observable} from "mobx";
import {getAsync} from "../../../../common/fetch/getAsync";

const userTicketsState = observable({
    list: null as unknown as ITicketList[]
})

const getUserTickets = action(async () => {
    return getAsync("/Ticket/GetUserTickets").then(response => {
        console.log(response.data.tickets)
        userTicketsState.list = response.data.tickets;
    })
})

export { userTicketsState, getUserTickets  }

interface ITicketList{
    id: number,
    title: string,
    condition: number,
    createDate: string
}
