import {getAsync} from "../../../../../common/fetch/getAsync";
import {option} from "../../../../../common/type/option";
import {action, observable} from "mobx";
import {postAsync} from "../../../../../common/fetch/postAsync";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";
import {getLocationListAsync} from "../list/store";
import {successToast} from "../../../../../common/toast/success";

const state = observable({form: null as unknown as IAddEditServer})

const getAddEditServerAsync = action(async (id: number) => {
    await getAsync(`/Admin/GetAddEditServer?id=${id}`)
        .then(response => {
            state.form = response.data
        })
})

const addServerAsync = action(async (form: any) => {
    form.locationId = form.locationId.value;

    await postAsync("/Admin/AddServer", form, () => {
        closeLastModal()
        successToast("  ")
        getLocationListAsync()
    })
})

const updateServerAsync = action(async (form: any) => {
    form.locationId = form.locationId.value;

    await postAsync("/Admin/UpdateServer", form, () => {
        closeLastModal()
        successToast("  ")
        getLocationListAsync()
    })
})

export {state, getAddEditServerAsync, addServerAsync, updateServerAsync}

interface IAddEditServer{
    ip: string,
    countryId: number,
    port: number,
    name: string,
    country: option,
    description: string,
    userName: string,
    password: string,

    countries: option[]
}

