import {action, observable} from "mobx";
import {IReferralPage} from "./model/IReferralPage";
import {successToast} from "../../../common/toast/success";
import {getAsync} from "../../../common/fetch/getAsync";

const state = observable({page: null as unknown as IReferralPage})

const getReferralPageAsync = action(async () => {
    return await getAsync("/User/GetReferralIndex").then(response => {
        state.page = response.data
    })
})

const copyLink = (link: string) => {
    navigator.clipboard.writeText(link).then(() => {
        successToast("  .")
    })
}

export {getReferralPageAsync, copyLink, state}
