import {observer} from "mobx-react-lite";
import {statisticsState} from "./store";

export const Index = observer(() => {

    const totalToday = statisticsState.connectionByLocations
        .reduce((a, b) => a + b.countToday, 0)
    const totalYesterday = statisticsState.connectionByLocations
        .reduce((a, b) => a + b.countYesterday, 0)
    const connectionTotal = statisticsState.connectionByLocations
        .reduce((a, b) => a + b.countTotal, 0)

    return <>
        <div className="d-block d-lg-flex justify-content-around">
            <div className="border border-1 rounded-2 text-center p-3">
                <div>
                      
                </div>
                {statisticsState.registerToday}
            </div>
            <div className="border border-1 rounded-2 text-center p-3">
                <div>
                      
                </div>
                {statisticsState.registerYesterday}
            </div>
            <div className="border border-1 rounded-2 text-center p-3">
                <div>
                     
                </div>
                {statisticsState.totalUsers}
            </div>
        </div>

        <div className="mt-3">
            <div className="d-none d-lg-flex justify-content-around border border-1 fw-bold" style={{height: "30px"}}>
                <div className="w-100 ms-2"></div>
                <div className="w-100 text-center"> </div>
                <div className="w-100 text-center"> </div>
                <div className="w-100 text-center"> </div>
            </div>
            {
                statisticsState.connectionByLocations?.map(statistic => {
                    return <div className="d-none d-lg-flex justify-content-around border-bottom border-1">
                        <div className="w-100 ms-2">{statistic.name}</div>
                        <div className="w-100 text-center">{statistic.countToday}</div>
                        <div className="w-100 text-center">{statistic.countYesterday}</div>
                        <div className="w-100 text-center">{statistic.countTotal}</div>
                    </div>
                })
            }

            <div className="d-none d-lg-flex justify-content-around border border-1 fw-bold" style={{height: "30px"}}>
                <div className="w-100 ms-2"></div>
                <div className="w-100 text-center">{totalToday}</div>
                <div className="w-100 text-center">{totalYesterday}</div>
                <div className="w-100 text-center">{connectionTotal}</div>
            </div>
        </div>
    </>

})