import {Button, Form, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {closeLastModal} from "../../../../../../common/component/modal/modalStore";
import {FormProvider, useForm} from "react-hook-form";
import {TextInput} from "../../../../../../common/component/input/text/TextInput";
import {TextAreaInput} from "../../../../../../common/component/input/text-area/TextAreaInput";
import {SelectInput} from "../../../../../../common/component/input/select/SelectInput";
import {useEffect} from "react";
import {createTicketAsync, getAddFormAsync, state} from "./store";
import {observer} from "mobx-react-lite";

export const Index = observer(() => {
    const hookForm = useForm();

    useEffect(() => {
        getAddFormAsync()
    }, []);

    return <>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit(createTicketAsync)}>
                <ModalHeader>
                    <ModalTitle> </ModalTitle>
                </ModalHeader>
                <ModalBody>
                    <TextInput name="title"
                               placeholder="   "
                               isRequired={true}/>
                    <SelectInput name="categoryId"
                                 isRequired={true}
                                 placeholder=" "
                                 options={state.form?.categories}/>
                    <TextAreaInput rows={4}
                                   name="message"
                                   isRequired={true}
                                   placeholder="  "/>
                </ModalBody>
                <ModalFooter>
                    <Button variant="dark" type="submit"></Button>
                    <Button variant="secondary" onClick={closeLastModal}></Button>
                </ModalFooter>
            </Form>
        </FormProvider>
    </>
})
