import {action, observable} from "mobx";
import {ILocation} from "../../model/ILocation";
import {getAsync} from "../../../../../common/fetch/getAsync";
import {postAsync} from "../../../../../common/fetch/postAsync";

const locationListState = observable({list: null as unknown as ILocation[]});

const getLocationListAsync = action(async () => {
    await getAsync("/Admin/GetIndexLocation").then(response => {
        locationListState.list = response.data.locations;
    })
})

const deleteServerAsync = action(async (id: number) => {
    await postAsync("/Admin/DeleteServer", {id: id}, () => {
        getLocationListAsync();
    })
})

export {locationListState, getLocationListAsync, deleteServerAsync}
