import {Button} from "react-bootstrap";
import {Link, useNavigate} from "react-router-dom";
import React, {useEffect, useRef} from "react";
import {observer} from "mobx-react-lite";
import {authState} from "../../../common/store/auth";

export const Index = observer(() => {
    const navigate = useNavigate();
    const loginBtnRef = useRef<HTMLButtonElement>(null)

    useEffect(() => {
        if(authState.token === null || authState.token === "")
            navigate("/login?auth_type=chrome")
        //else
            //loginBtnRef.current?.click()
    }, [])

    const loginIn = () => {
        navigate("/?login_in=success_chrome")
    }

    return <div className="d-flex justify-content-center align-items-center vh-100">
        <div className="text-center">
            <Link to="/" className="text-center text-decoration-none text-black mb-4">
                <h1 className="fw-bold">LockVPN</h1>
                <h5>  </h5>
            </Link>
            <Button ref={loginBtnRef} variant="dark" id="login_in" onClick={loginIn}>
                  
            </Button>
        </div>
    </div>
})