import {FormProvider, useForm} from "react-hook-form";
import {Alert, Button, Form} from "react-bootstrap";
import {Link, useNavigate} from "react-router-dom";
import {TextInput} from "../../../../common/component/input/text/TextInput";
import React, {useEffect} from "react";
import {SetPasswordInputs} from "../../../../common/component/input/set-password/SetPasswordInputs";
import {isNewPasswordSet, setNewPasswordAsync} from "../store";
import {observer} from "mobx-react-lite";

export const SetNewPassword = observer((props: {guid: string}) => {
    const hookForm = useForm();

    const navigate = useNavigate()

    useEffect(() => {
        if(isNewPasswordSet.value)
            setTimeout(() => navigate("/login"), 3000)
    }, [isNewPasswordSet.value]);

    return <>
        {!isNewPasswordSet.value &&
            <FormProvider {...hookForm}>
                <Form onSubmit={hookForm.handleSubmit(setNewPasswordAsync)}>
                    <div className="d-flex flex-column justify-content-center align-items-center vh-100">
                        <div className="d-flex flex-column align-items-center login-form">
                            <Link to="/" className="text-center text-decoration-none text-black mb-4">
                                <h1 className="fw-bold">LockVPN</h1>
                                <h5> </h5>
                            </Link>

                            <SetPasswordInputs isRequired={true}/>
                            <TextInput name="guid"
                                       attributes={{className: "d-none"}}
                                       defaultValue={props.guid}/>

                            <Button variant="dark" type="submit" className="mt-2">
                                 
                            </Button>
                        </div>
                    </div>
                </Form>
            </FormProvider>
        }

        {isNewPasswordSet.value &&
            <div className="d-flex justify-content-center align-items-center vh-100 w-100">
                <Alert className="alert-container text-center" variant="dark">
                       .       .
                </Alert>
            </div>
        }
    </>
})