import {observer} from "mobx-react-lite";
import {FormProvider, useForm} from "react-hook-form";
import {Button, Form, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {useEffect} from "react";
import {getPositionAsync, statePosition, updatePositionAsync} from "./store";
import {TextInput} from "../../../../../common/component/input/text/TextInput";
import {NumberInput} from "../../../../../common/component/input/number/NumberInput";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";

export const AddEditAccessPositionModal = observer((props: {id: number}) => {
    const hookForm = useForm()

    useEffect(() => {
        getPositionAsync(props.id)
    }, []);

    return <>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit(updatePositionAsync)}>
                <ModalHeader>
                    <ModalTitle>{props.id === 0 ? " " : " "}</ModalTitle>
                </ModalHeader>
                <ModalBody>
                    <TextInput name="id"
                               isRequired={true}
                               defaultValue={statePosition.positions?.id.toString()}
                               attributes={{className: "d-none"}}/>
                    <TextInput name="name"
                               label=":"
                               isRequired={true}
                               defaultValue={statePosition.positions?.name} />
                    <TextInput name="description"
                               label=":"
                               defaultValue={statePosition.positions?.description} />
                    <NumberInput name="monthCount"
                                 label=" :"
                                 isRequired={true}
                                 defaultValue={statePosition.positions?.monthCount.toString()}/>
                    <NumberInput name="price"
                                 label=":"
                                 isRequired={true}
                                 defaultValue={statePosition.positions?.price.toString()}/>
                    <TextInput name="googlePlayIdentifier"
                               label=" GP:"
                               isRequired={true}
                               defaultValue={statePosition.positions?.googlePlayIdentifier} />
                </ModalBody>
                <ModalFooter>
                    <Button variant="dark" type="submit"></Button>
                    <Button variant="danger" onClick={closeLastModal}></Button>
                </ModalFooter>
            </Form>
        </FormProvider>
    </>
})