import {action, observable} from "mobx";
import {getAsync} from "../../../common/fetch/getAsync";

const state = observable({activationIsSuccess: null as unknown as boolean})

const sendActivation = action(async (guid:string) => {
    await getAsync(`/User/Activation?guid=${guid}`)
        .then(response => {
            state.activationIsSuccess = true
        })
})

export {state, sendActivation}
