import {state} from "../store";
import {Button} from "react-bootstrap";
import {List} from "react-bootstrap-icons";

export const ReferralList = () => {
    return <div className="mt-4">
        <div className="d-flex text-center pb-2 mb-1 border-bottom border-1">
            <div className="w-50 fw-bold">
                
            </div>
            <div className="w-50 fw-bold">
                
            </div>
        </div>
        <div>
            {
                state.page?.referrals?.map(referral => {
                    return <div key={referral.userId} className="d-flex text-center py-2 border-bottom border-1">
                        <div className="w-50">
                            {referral.name}
                        </div>
                        <div className="w-50">
                            {referral.topupTotal}
                        </div>
                    </div>
                })
            }
        </div>

        {
            state.page.referrals?.length === 0 &&
            <div className="text-center text-secondary pt-2">
                <div>  .</div>
                <div>       10%      .</div>
            </div>

        }
    </div>
}
