import {FormProvider, useForm} from "react-hook-form";
import {Alert, Button, Form, FormGroup} from "react-bootstrap";
import {Link} from "react-router-dom";
import {TextInput} from "../../../../common/component/input/text/TextInput";
import HCaptcha from "@hcaptcha/react-hcaptcha";
import React, {useRef} from "react";
import {createResetPasswordLinkAsync, isLinkCreat} from "../store"
import {mainStore} from "../../../../common/mainStore";
import {observer} from "mobx-react-lite";
import {getTranslate} from "../../../../common/utils/localization";


export const CreateResetPasswordLink = observer(() => {
    const hookForm = useForm();
    const captchaRef = useRef<HCaptcha>(null)

    return <>
        {!isLinkCreat.value &&
            <FormProvider {...hookForm}>
                <Form onSubmit={hookForm.handleSubmit(createResetPasswordLinkAsync)}>
                    <div className="d-flex flex-column justify-content-center align-items-center vh-100">
                        <div className="d-flex flex-column align-items-center login-form">
                            <Link to="/" className="text-center text-decoration-none text-black mb-4">
                                <h1 className="fw-bold">LockVPN</h1>
                                <h5>{getTranslate(" ")}</h5>
                            </Link>

                            <TextInput label={`${getTranslate(" ")}:`}
                                       name="email"
                                       isRequired={true}
                                       placeholder={`${getTranslate("  ")}`}/>
                            {mainStore.captchaPublicKey &&
                                <FormGroup className="text-center">
                                    <HCaptcha ref={captchaRef}
                                              sitekey={mainStore.captchaPublicKey}
                                              onVerify={(token) => hookForm.setValue("token", token)}
                                    />
                                </FormGroup>
                            }
                            <TextInput name="token"
                                       attributes={{className: "d-none"}}
                                       isRequired={true}/>

                            <Button variant="dark" type="submit" className="mt-2" onClick={() => captchaRef.current?.resetCaptcha()}>
                                {getTranslate(" ")}
                            </Button>
                        </div>
                    </div>
                </Form>
            </FormProvider>
        }

        {isLinkCreat.value &&
            <div className="d-flex justify-content-center align-items-center vh-100 w-100">
                <Alert className="alert-container text-center" variant="dark">
                                (  ).
                </Alert>
            </div>
        }
    </>
})