import {Button, FormControl, InputGroup} from "react-bootstrap";
import {copyLink, state} from "../store";
import {ClipboardFill} from "react-bootstrap-icons";
import {useRef} from "react";

export const Toolbar = () => {
    const referralLink = useRef<HTMLInputElement>(null)

    return <div>
        <h6> :</h6>
        <InputGroup>
            <FormControl ref={referralLink} aria-describedby="with-copy-btn" value={state.page?.referralLink} disabled/>
            <Button id="with-copy-btn" variant="dark" onClick={() => copyLink(referralLink.current!.value)}>
                <ClipboardFill width={16} height={16}/>
            </Button>
        </InputGroup>
    </div>
}
