import {observer} from "mobx-react-lite";
import {FormProvider, useForm} from "react-hook-form";
import {Button, Form, FormControl, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";
import {useEffect} from "react";
import {getUserAsync, state, updateAsync} from "./store";
import {TextInput} from "../../../../../common/component/input/text/TextInput";
import {DateInput} from "../../../../../common/component/input/date/DateInput";
import {yyyy_mm_dd} from "../../../../../common/utils/dateFunctions";
import {SetPasswordInputs} from "../../../../../common/component/input/set-password/SetPasswordInputs";

export const AddEdit = observer((props: {id: number}) => {
    const hookForm = useForm()
    const accessEndDate = yyyy_mm_dd(new Date(state.user?.accessEndDate))

    useEffect(() => {
        getUserAsync(props.id)
    }, []);



    return <>
        <FormProvider {...hookForm}>
            <Form onSubmit={hookForm.handleSubmit(updateAsync)}>
                <ModalHeader>
                    <ModalTitle> </ModalTitle>
                </ModalHeader>
                <ModalBody>
                    <TextInput name="id"
                               isRequired={true}
                               defaultValue={props.id.toString()}
                               attributes={{className: "d-none"}}/>
                    <TextInput
                        label=":"
                        name="login"
                        isRequired={true}
                        defaultValue={state.user?.name}/>

                    <DateInput
                        label="  :"
                        name="accessEndDate"
                        defaultValue={accessEndDate}/>

                    <SetPasswordInputs isRequired={false}/>
                </ModalBody>
                <ModalFooter>
                    <Button variant="dark" type="submit">
                        
                    </Button>
                    <Button variant="danger" onClick={closeLastModal}>
                        
                    </Button>
                </ModalFooter>
            </Form>
        </FormProvider>
    </>
})