import {observer} from "mobx-react-lite";
import {Accordion, AccordionHeader, AccordionItem, Button} from "react-bootstrap";
import {deleteServerAsync, locationListState} from "./store";
import AccordionBody from "react-bootstrap/AccordionBody";
import {PencilFill, TrashFill} from "react-bootstrap-icons";
import {openModal} from "../../../../../common/component/modal/modalStore";
import {AddEditServer} from "../add-edit-server/AddEditServer";
import {size} from "../../../../../common/component/modal/IModal";

export const List = observer(() => {
    return <>
        <Accordion>
            {
                locationListState.list?.map(location => {
                    return <AccordionItem eventKey={location.id.toString()}>
                        <AccordionHeader>{location.name}</AccordionHeader>
                        <AccordionBody>

                            {
                                location.servers.map(server => {
                                    return <div key={server.id} className="d-flex justify-content-between mb-2">
                                        <div className="w-100">{server.id}</div>
                                        <div className="w-100">{server.name}</div>
                                        <div className="w-100">{server.description}</div>
                                        <div className="w-100">{server.ip}</div>
                                        <div className="w-100">{server.port}</div>
                                        <div className="w-100 d-flex justify-content-center">
                                            <Button className="bg-second me-2"
                                                    onClick={() => openModal({modal: <AddEditServer id={server.id}/>, size: size.lg})}>
                                                <PencilFill/>
                                            </Button>
                                            <Button variant="danger" onClick={() => deleteServerAsync(server.id)}>
                                                <TrashFill/>
                                            </Button>
                                        </div>
                                    </div>
                                })
                            }
                        </AccordionBody>
                    </AccordionItem>
                })
            }
        </Accordion>
    </>

})
