import {action, observable} from "mobx";
import {getAsync} from "../../../../../common/fetch/getAsync";
import {errorToast} from "../../../../../common/toast/error";
import {base64toObjectURL} from "../../../../../common/utils/base64StringHelper";

interface IPaymentPosition{
    id: number,
    name: string,
    price: number,
    monthCount: number
}

const state = observable({
    positions: null as unknown as IPaymentPosition[],
    endAccessDate: null as unknown as string,

    selectPositionId: 0,
    newAccessDate: null as unknown as string,
    price: 0,
    promoCode: ""
})

const getPaymentPositionAsync = action(async () => {
    await getAsync("/Payment/GetPaymentPositions").then(response => {
        state.positions = response.data.accessPositions;
        state.endAccessDate = response.data.endAccessDate;
        state.newAccessDate = new Date(response.data.endAccessDate).toLocaleDateString();

        state.selectPositionId = 0;
        state.price = 0;
    })
})

const applyPromoCodeAsync = action(async (promoCode: string) => {

    if(state.selectPositionId === 0){
        errorToast("  ")
        return;
    }

    if(promoCode === ""){
        errorToast(" ")
        return;
    }

    await getAsync(`/Payment/applyPromoCode?promoCode=${promoCode}&positionId=${state.selectPositionId}`)
        .then(response => {
            if(response.status === 200){
                state.price = response.data;
                state.promoCode = promoCode;
            }
        })
})


const requestYouKassaPaymentLinkAsync = action(async () => {

    if(state.selectPositionId === 0){
        errorToast("  ");
        return;
    }

    await getAsync(`/Payment/GetYouKassaLink?id=${state.selectPositionId}&promocode=${state.promoCode}`).then(response => {
        if(response.status === 200)
            window.open(response.data, "_blank")
    })
})

const requestCryptoCloudPaymentLinkAsync = action(async () => {

    if(state.selectPositionId === 0){
        errorToast("  ");
        return;
    }

    await getAsync(`/Payment/GetCryptoCloudLink?id=${state.selectPositionId}&promocode=${state.promoCode}`).then(response => {
        if(response.status === 200)
            window.open(response.data, "_blank")
    })
})

const selectPosition = action((id: number) => {
    const position = state.positions.find(x => x.id === id)
    state.selectPositionId = position!.id;

    const date = new Date(state.endAccessDate)
    state.newAccessDate = new Date(date.setMonth(date.getMonth() + position!.monthCount)).toLocaleDateString()
    state.price = position!.price
})

export {
    state,
    getPaymentPositionAsync,
    selectPosition, applyPromoCodeAsync, requestYouKassaPaymentLinkAsync, requestCryptoCloudPaymentLinkAsync }