import {action, observable} from "mobx";
import {getAsync} from "./fetch/getAsync";
import {postAsync} from "./fetch/postAsync";
import {successToast} from "./toast/success";

const mainStore : IMainStore = observable({
    isLoading: false,
    captchaPublicKey: "",
    currencyList: []
})

const getMainStore = action(async () => {
    await getAsync("/Admin/GetSettings")
        .then(response => {
            mainStore.captchaPublicKey = response.data.captchaPublicKey;
            mainStore.currencyList = response.data.currencyList;
        })
})

const confirmChangePasswordAsync = action(async (guid: string) => {
    await postAsync("/User/ConfirmChangePassword", {
        guid: guid,
    }, (response) => {
        if(response.status === 200)
            successToast("  ")
    })
})

const confirmChangeEmailAsync = action(async (guid: string) => {
    await postAsync("/User/ConfirmChangeEmail", {
        guid: guid,
    }, (response) => {
        if(response.status === 200)
            successToast("    ")
    })
})

export {mainStore, getMainStore, confirmChangePasswordAsync, confirmChangeEmailAsync}

interface IMainStore{
    captchaPublicKey: string,
    isLoading: boolean,
    currencyList: ICurrency[]
}

interface ICurrency{
    charCode: string,
    value: number
}