import {sendMessageAsync, ticketViewState} from "./store";
import {MessageList} from "./component/MessageList";
import {observer} from "mobx-react-lite";
import {Button, FormControl} from "react-bootstrap";
import {useParams} from "react-router-dom";
import {useRef} from "react";

export const Index = observer(() => {
    const {ticketId} = useParams();
    const messageInputRef = useRef<HTMLTextAreaElement>(null)

    return <div>
        <div>
            <h4>#{ticketViewState.ticket?.id} - {ticketViewState.ticket?.title}</h4>
        </div>
        <hr className="my-0"/>
        <MessageList/>
        <div className="d-flex w-100">
            <FormControl className="me-3" ref={messageInputRef} as="textarea" rows={2} placeholder=" "/>
            <Button variant="dark" onClick={() => {
                sendMessageAsync(ticketId!, messageInputRef.current!.value);
                messageInputRef.current!.value = "";
            }}>
                
            </Button>
        </div>
    </div>
})
