import {Link} from "react-router-dom";
import React from "react";
import {HouseFill, PersonFill} from "react-bootstrap-icons";
import {getTranslate, localizationState} from "../../../../utils/localization";
import {observer} from "mobx-react-lite";
import {Image} from "react-bootstrap";
import {openModal} from "../../../../component/modal/modalStore";
import {LanguageModal} from "../../modals/LanguageModal";

export const TopMenu = observer(() => {

    return <>
        <div className="d-flex h-100">
            <Link to="/"
                  className="d-flex align-items-center px-1 px-lg-3 h-100 text-decoration-none text-white horizontal-menu-item">
                <HouseFill className="d-block d-md-none me-2" width={25} height={25} color="white"/>
                <span className="d-none d-lg-block">{getTranslate("")}</span>
            </Link>
            <Link to="/login"
                  className="d-flex align-items-center px-1 px-lg-3 h-100 text-decoration-none text-white horizontal-menu-item">
                <PersonFill className="d-block d-md-none me-2" width={25} height={25} color="white"/>
                <span className="d-none d-lg-block">{getTranslate(" ")}</span>
            </Link>
        </div>

        <div className="d-flex mx-2 mx-lg-0">
            <div className="text-center rounded-2 bg-white"
                 style={{cursor: "pointer"}}
                 onClick={() => openModal({modal: <LanguageModal/>, size: undefined})}>
                <Image style={{maxHeight: "30px"}}
                       src={`${window.location.origin}/img/flags/${localizationState.lang}.svg`}
                       fluid={true}/>
            </div>
        </div>
    </>
})