import {action, observable} from "mobx";
import {IUser} from "./model/IUser";
import { jwtDecode } from "jwt-decode";
import {googleLogout} from "@react-oauth/google";


const authState = observable({
    token: localStorage.getItem("token"),
    user: localStorage.getItem("token") ? jwtDecode(localStorage.getItem("token")!) as IUser : null as unknown as IUser
});

const setToken = action((token: string) => {
    authState.token = token;
    authState.user = jwtDecode(token) as IUser;
})

const removeToken = action(() => {
    localStorage.removeItem("token")
    authState.token = null;
})

export {authState, setToken, removeToken}
