import {Badge, FormControl, FormGroup, FormLabel} from "react-bootstrap";
import {ITextAreaInput} from "./ITextAreaInput";
import {useFormContext} from "react-hook-form";

export const TextAreaInput = (props: ITextAreaInput) => {
    const {register, formState: {errors}} = useFormContext();
    const isRequired = props.isRequired || false;

    return <FormGroup>
        {props.label &&
            <FormLabel>{props.label}</FormLabel>
        }
        <FormControl {...register(props.name, {
            required: {value: isRequired, message: "   "}
        })} as="textarea" rows={props.rows} placeholder={props.placeholder}/>
        {errors[props.name]?.message &&
            <Badge bg="warning" className="mt-2">{errors[props.name]?.message?.toString()}</Badge>
        }
    </FormGroup>
}
