import React from "react";
import {Button} from "react-bootstrap";
import {useNavigate} from "react-router-dom";
import {getTranslate, localizationState} from "../../../../utils/localization";
import {mainStore} from "../../../../mainStore";
import {observer} from "mobx-react-lite";

export const Price = observer(() => {
    const navigation = useNavigate()


    return <div className="w-100">
        <div className="mt-3">
            <h2 className="text-white"> </h2>
        </div>
        <div className="d-block d-lg-flex justify-content-around w-100 text-white mt-3">
            <div className="border border-1 rounded-2 py-1 px-2 mx-2 my-lg-0 my-2">{getTranslate("   3 ")}</div>
            <div className="border border-1 rounded-2 py-1 px-2 mx-2 my-lg-0 my-2">{getTranslate("  Windows       Chromium")}</div>
            <div className="border border-1 rounded-2 py-1 px-2 mx-2 my-lg-0 my-2">{getTranslate("    (Android)")}</div>
            <div className="border border-1 rounded-2 py-1 px-2 mx-2 my-lg-0 my-2">{getTranslate("   VPN    (Android)")}</div>
        </div>
        <div className="d-block d-lg-flex justify-content-around w-100 bg-second py-lg-5">
            <div
                className="d-flex flex-column flex-md-row justify-content-md-around justify-content-center align-items-center flex-fill my-3 my-lg-0">
                <div className="price-box py-2 my-3 my-md-0">
                    <div className="pb-2">
                        <h3 className="fw-bold">{getTranslate(" 1 ")}</h3>
                    </div>
                    <div className="pb-2">
                        <div className="pb-2">200{getTranslate("/")}</div>
                        <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                            <div>
                                {getTranslate("  ")}200{getTranslate("")}
                            </div>
                            {localizationState.lang !== "ru-ru" &&
                                <div>
                                    (~{`${(200 / mainStore.currencyList[0]?.value + 1.5).toFixed(1)} ${mainStore.currencyList[0]?.charCode}`})
                                </div>
                            }
                        </Button>
                    </div>
                </div>
                <div className="price-box py-2 my-3 my-md-0">
                    <div className="pb-2">
                        <h3 className="fw-bold">{getTranslate(" 3 ")}</h3>
                    </div>
                    <div className="pb-2">
                        <div className="pb-2">180{getTranslate("/")}</div>
                        <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                            {getTranslate("  ")}540{getTranslate("")}
                            {localizationState.lang !== "ru-ru" &&
                                <div>
                                    (~{`${(540 / mainStore.currencyList[0]?.value + 1.5).toFixed(1)} ${mainStore.currencyList[0]?.charCode}`})
                                </div>
                            }
                        </Button>
                    </div>
                </div>
            </div>
            <div
                className="d-flex flex-column flex-md-row justify-content-md-around justify-content-center align-items-center flex-fill my-3 my-lg-0">
                <div className="price-box py-2 my-3 my-md-0">
                    <div className="pb-2">
                        <h3 className="fw-bold">{getTranslate(" 6 ")}</h3>
                    </div>
                    <div className="pb-2">
                        <div className="pb-2">160{getTranslate("/")}</div>
                        <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                            {getTranslate("  ")}960{getTranslate("")}
                            {localizationState.lang !== "ru-ru" &&
                                <div>
                                    (~{`${(960 / mainStore.currencyList[0]?.value + 1.5).toFixed(1)} ${mainStore.currencyList[0]?.charCode}`})
                                </div>
                            }
                        </Button>
                    </div>
                </div>
                <div className="price-box py-2 my-3 my-md-0">
                    <div className="pb-2">
                        <h3 className="fw-bold">{getTranslate(" 1 ")}</h3>
                    </div>
                    <div className="pb-2">
                        <div className="pb-2">140{getTranslate("/")}</div>
                        <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                            {getTranslate("  ")}1680{getTranslate("")}
                            {localizationState.lang !== "ru-ru" &&
                                <div>
                                    (~{`${(1680 / mainStore.currencyList[0]?.value + 1.5).toFixed(1)} ${mainStore.currencyList[0]?.charCode}`})
                                </div>
                            }
                        </Button>
                    </div>
                </div>
            </div>
        </div>
    </div>


})