import {useSearchParams} from "react-router-dom";
import {useEffect} from "react";
import {confirmChangePasswordAsync} from "../../mainStore";

export const ConfirmChangePasswordRequest = () => {
    const [searchParams, setSearchParams] = useSearchParams()

    useEffect(() => {
        if(searchParams.has("confirm-change-password-request-guid")){
            confirmChangePasswordAsync(searchParams.get("confirm-change-password-request-guid") || "")
            searchParams.delete("confirm-change-password-request-guid")
            setSearchParams(searchParams)
        }
    }, []);

    return <></>
}