import {observer} from "mobx-react-lite";
import {
    Button,
    FormCheck,
    FormControl,
    FormGroup,
    FormLabel,
    ModalBody,
    ModalFooter,
    ModalHeader,
    ModalTitle
} from "react-bootstrap";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";
import {useEffect, useRef, useState} from "react";
import {
    applyPromoCodeAsync,
    getPaymentPositionAsync,
    requestCryptoCloudPaymentLinkAsync,
    requestYouKassaPaymentLinkAsync,
    selectPosition,
    state
} from "./store";
import {mainStore} from "../../../../../common/mainStore"
import Select from "react-select";
import {PaymentMethod} from "../../model/PaymentMethod";
import {getTranslate, localizationState} from "../../../../../common/utils/localization";

export const PaymentModal = observer(() => {
    const promoCodeRef = useRef<HTMLInputElement>(null);
    const [paymentMethod, setPaymentMethod] = useState<PaymentMethod>(PaymentMethod.YouKassa)

    useEffect(() => {
        getPaymentPositionAsync()
    }, []);

    const paymentMethods = [
        {value: PaymentMethod.YouKassa, label: getTranslate("  (, , ,  , Express Pay)")},
        {value: PaymentMethod.CryptoCloud, label: getTranslate("")},
        //{value: 3, label: " "},
    ]

    return <>
        <ModalHeader>
            <ModalTitle>{getTranslate(" ")}</ModalTitle>
        </ModalHeader>
        <ModalBody>
            <div className="mt-3 d-lg-flex d-block">
                <div className="w-100 text-center me-2 mb-lg-0 mb-3">
                    {
                        state.positions?.map(position => {
                            return <div key={position.id}
                                        className="d-flex align-items-center mt-2 border-bottom border-1 mx-3 p-2"
                                        onClick={() => selectPosition(position.id)}>
                                <div className="w-100">
                                    {getTranslate(position.name)}
                                </div>
                                <div className="w-100">
                                    <div>
                                        {position.price + `${getTranslate("")}.`}
                                    </div>

                                    {localizationState.lang !== "ru-ru" &&
                                        <div>
                                            (~{`${(position.price / mainStore.currencyList[0]?.value + 1.5).toFixed(1)} ${mainStore.currencyList[0]?.charCode}`})
                                        </div>
                                    }
                                </div>
                                <div className="w-100">
                                    <FormCheck type="radio"
                                               name="access"
                                               checked={position.id === state.selectPositionId}/>
                                </div>
                            </div>
                        })
                    }
                </div>
                <div className="d-flex flex-column justify-content-around w-100">
                    <FormGroup className="mx-3">
                        <FormLabel className="fw-bold">{getTranslate("")}:</FormLabel>
                        <div className="d-flex">
                            <FormControl size="sm"
                                         className="me-2 w-100" ref={promoCodeRef}/>
                            <Button variant="dark"
                                    size="sm"
                                    onClick={() => applyPromoCodeAsync(promoCodeRef.current?.value || "")}>
                                {getTranslate("")}
                            </Button>
                        </div>
                        <hr className="py-0 mt-3"/>
                    </FormGroup>

                    <div className="mx-3">
                        <div className="mb-4">
                            <FormLabel className="fw-bold">{getTranslate(" ")}:</FormLabel>
                            <Select options={paymentMethods}
                                    defaultValue={paymentMethods[0]}
                                    onChange={(value) => {
                                        setPaymentMethod(value!.value)
                                    }}/>
                        </div>

                        <div className="mb-2">
                            <FormLabel className="me-2 fw-bold">{getTranslate("  ")}: </FormLabel>
                            {state?.newAccessDate}
                        </div>
                        <div className="mb-2">
                            <FormLabel className="me-2 fw-bold">{getTranslate(" ")}: </FormLabel>
                            {`${state?.price}. `}
                            {localizationState.lang !== "ru-ru" && state?.price > 0 &&
                                <>
                                    (~{`${(state?.price / mainStore.currencyList[0]?.value + 1.5).toFixed(1)} ${mainStore.currencyList[0]?.charCode}`})
                                </>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </ModalBody>
        <ModalFooter>
            <Button variant="secondary" size="sm" onClick={closeLastModal}>
                {getTranslate("")}
            </Button>

            <Button size="sm" variant="dark" onClick={() => {
                if(paymentMethod === PaymentMethod.YouKassa)
                    requestYouKassaPaymentLinkAsync()
                else if(paymentMethod === PaymentMethod.CryptoCloud)
                    requestCryptoCloudPaymentLinkAsync()
            }}>
                {getTranslate("")}
            </Button>

        </ModalFooter>
    </>
})