import {action, observable} from "mobx";
import {postAsync} from "../../../../../common/fetch/postAsync";
import {successToast} from "../../../../../common/toast/success";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";
import {IPromoCode} from "./IPromoCode";
import {getAsync} from "../../../../../common/fetch/getAsync";

const stateList = observable({list: null as unknown as IPromoCode[]})
const state = observable({code: null as unknown as IPromoCode})

const getPromoCodes = action(async () => {
    await getAsync("/admin/getPromoCodes")
        .then(response => stateList.list = response.data)
})

const getPromoCode = action(async (id: number) => {
    await getAsync(`/admin/getPromoCode?id=${id}`)
        .then(response => state.code = response.data)
})

const addPromoCodeAsync = action(async (form: any) => {
    await postAsync("/admin/addPromoCode", form, (response) => {
        if (response.status === 200){
            successToast(" ")
            closeLastModal()
            getPromoCodes()
        }
    });
})

const deletePromoCodeAsync = action(async (id: number) => {
    await postAsync("/admin/deletePromoCode", {id: id}, (response) => {
        if (response.status === 200){
            successToast(" ")
            getPromoCodes()
        }
    })
})

const updatePromoCodeAsync = action(async (form: any) => {
    await postAsync("/admin/updatePromoCode", form, (response) => {
        if (response.status === 200){
            successToast(" ")
            closeLastModal()
            getPromoCodes()
        }
    })
})

const resetPromoCode = action(() => {
    state.code = null as unknown as IPromoCode;
})

export {state, stateList, getPromoCode, getPromoCodes, addPromoCodeAsync, deletePromoCodeAsync, updatePromoCodeAsync, resetPromoCode}