import {observer} from "mobx-react-lite";
import {mainStore} from "../mainStore";

export const Loading = observer(() => {

    if(!mainStore.isLoading)
        return <></>

    return <div className="d-flex justify-content-center align-items-center"
                style={{position: "absolute", top: "0", width: "100%", height: "100vh", zIndex: "100", background: "white"}}>
        <img width={150} height={150} src={`${window.location.origin}/img/loading.gif`}/>
    </div>
})