import {action, observable, runInAction} from "mobx";
import {postAsync} from "../../../common/fetch/postAsync";
import {getAsync} from "../../../common/fetch/getAsync";
import {IDirectory} from "./models/IDirectory";
import {ChangeEvent} from "react";
import path from "node:path";
import {successToast} from "../../../common/toast/success";

interface IFiles{
    uploadPath: string,
    directories: IDirectory[]
}

let state = observable({value: null as unknown as IFiles});

const getDirectoriesAsync = action(async () => {
    await getAsync("/Admin/GetDirectories").then(response => {
        state.value = response.data
    })
})

const createDirectoryAsync = action(async (form: any) => {
    await postAsync("/Admin/CreateDirectory", {
        name: form.name
    }, () => {
        getDirectoriesAsync()
    })
})

const deleteDirectoryAsync = action(async (path: string) => {
    await postAsync("/Admin/DeleteDirectory", {
        path: path
    }, () => {
        getDirectoriesAsync()
    })
})

const uploadFileAsync = action(async (event: ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files![0];

    console.log(state.value.uploadPath)

    let reader = new FileReader();
    reader.readAsDataURL(file)

    reader.onload = () => {
            postAsync("/Admin/UploadFile", {
                fileName: file.name,
                data: reader.result,
                path: state.value.uploadPath
            }, () => {
                event.target.value = ""
                successToast(" ")
                getDirectoriesAsync()
            })
    }
})

const deleteFileAsync = action(async (path: string, fileName: string) => {
    await postAsync("/Admin/DeleteFile", {
        path: path,
        fileName: fileName
    }, () => {
        successToast(" ")
        getDirectoriesAsync()
    })
})

const copyLink = (link: string) => {
    navigator.clipboard.writeText(link).then(() => {
        successToast("  .")
    })
}

export { state, getDirectoriesAsync, createDirectoryAsync, deleteDirectoryAsync, uploadFileAsync, deleteFileAsync, copyLink }