import {action, observable} from "mobx";
import {getAsync} from "../../../../common/fetch/getAsync";

const state = observable({
    tickets: null as unknown as ITicket[]
})

const getTicketsAsync = action(async () => {
    await getAsync("/Admin/GetListTicketManagement").then(response => {
        state.tickets = response.data.tickets
    })
})

export {getTicketsAsync, state}

interface ITicket{
    id: number,
    title: string,
    category: string,
    userName: string,
    createDate: string,
    condition: number
}
